.chat{
    flex: 0.65;
    display: flex;
    flex-direction: column;
    /* display: block; */
    flex-direction: column;
}

.chat__header{
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid lightgray;
    /* place-items: center;
    justify-content: space-between; */
}
.chat__headerInfo{
    flex:1;
    padding-left: 20px;
}
.chat__headerInfo > h3{
    margin-bottom: 3px;
    font-weight: 500;
}
.chat__headerInfo >p{
    color: gray;
}
.chat__headerRight{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}
.chat__body{
    flex: 1;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;
}
.chat__message{
    position: relative;
    font-size: small;
    padding: 10px;
    background-color: white;
    border-radius: 10px;
    width: fit-content;
    margin-bottom: 10px;
}
.chat__receiver{
    margin-left: auto;
    background-color: #dcf8c6;
}
.chat__name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}
.chat__timeStamp{
    margin-left: 10px;
    font-size: xx-small;
}
.chat__footer{ 
    padding: 10px;   
    align-items: center;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid lightgray;
}
.chat__footer > form {
    flex:1;
    display: flex;    
}
.chat__footer > form > input{
    flex: 1;
    border-radius: 30px;
    border: none;
    padding: 10px;
}
.chat__footer > form > button{
    display: none;
}
.chat__footer > .MuiSvgIcon-root{
    padding: 10px;
    color: gray;
}
